Vue.filter('NumberFormat', function(value) {
  if (!value) {
    return '0'
  }
  const intPartFormat = value.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,') // 将整数部分逢三一断
  return intPartFormat
})

Vue.filter('fileSizeFormat', function(value) {
  if (!value) {
    return '0 Bytes'
  }
  var unitArr = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB']
  var index = 0
  var srcsize = parseFloat(value)
  index = Math.floor(Math.log(srcsize) / Math.log(1024))
  var size = srcsize / Math.pow(1024, index)
  size = size.toFixed(2)
  return size + ' ' + unitArr[index]
})

Vue.filter('dayTime', function(value) {
  var days = Math.floor(value / 86400)
  var hours = Math.floor((value % 86400) / 3600)
  var minutes = Math.floor(((value % 86400) % 3600) / 60)
  var seconds = Math.floor(((value % 86400) % 3600) % 60)
  var duration = days + 'd ' + hours + 'h ' + minutes + 'm ' + seconds + 's'
  return duration
})
Vue.filter('formatTimestamp', function(value) {
  // return new Date(parseInt(value) * 1000).toLocaleString().replace(/:\d{1,2}$/,' ');
  return new Date(parseInt(value)).toLocaleString().replace(/:\d{1,2}$/,' ');
})
Vue.filter('formatTimestampToDay', function(value) {
  var n = parseInt(value);
  var D = new Date(n);
  var year = D.getFullYear();//四位数年份

  var month = D.getMonth()+1;//月份(0-11),0为一月份
  month = month<10?('0'+month):month;

  var day = D.getDate();//月的某一天(1-31)
  day = day<10?('0'+day):day;

  var hours = D.getHours();//小时(0-23)
  hours = hours<10?('0'+hours):hours;

  var minutes = D.getMinutes();//分钟(0-59)
  minutes = minutes<10?('0'+minutes):minutes;

// var seconds = D.getSeconds();//秒(0-59)
// seconds = seconds<10?('0'+seconds):seconds;
// var week = D.getDay();//周几(0-6),0为周日
// var weekArr = ['周日','周一','周二','周三','周四','周五','周六'];

  // var now_time = year+'-'+month+'-'+day+' '+hours+':'+minutes;
  var now_time = year+'/'+month+'/'+day;
  return now_time;
})




